import { h } from 'preact'
import { Component } from 'preact'
import ObjectForm from './Forms/Object.jsx'

class AddObject extends Component {
  /**
   * Add object is the page
   */
  render ({ onLoad, id }) {
    if (id) {
      // edit
      return (
        h(ObjectForm,{
          id:id,
          title:" ",path:"/admin-data?objects",
          successMessage:"  !",
          confirmText:""}))
    }
    return (
      h(ObjectForm,{ submitFinish:async (res) => {
        if (onLoad) onLoad(res)
        const d = await res.json()
        const { data } = d
        this.setState({ id: data })
      },title:" ", addedId:this.state.id, path:"/admin-data?objects", successMessage:"  !", confirmText:"" }))
  }
}

export default AddObject