import { h } from 'preact'
import { Component } from 'preact'
import { Col, Row, Icon, A } from './../../frontend/components/Bootstrap.jsx'
import DeleteModal, { EditModal } from './../DeleteModal.jsx'
import { loadData } from './../Components/LoadData.jsx'
import CategoryForm from './Forms/Category.jsx'
// import 'preact/devtools/'

export default class Categories extends Component {
  constructor() {
    super()
    this.state = {
      loading: true,
      data: [],
    }
  }
  async componentDidMount() {
    await this.load()
  }
  async load() {
    /** @type {Array<Category>} */
    const data = await loadData.bind(this)('categories')
    if (data) this.setState({ data })
  }
  openModal(modal) {
    this.setState({ modal })
  }
  openEdit(edit) {
    this.setState({ edit })
  }
  render() {
    const { loading, data, modal, edit } = this.state
    return                                                                     h(Col,{},
      h('h1',{},` `),
      h('p',{},
        `     :`
      ),
      h(List,{data:data,loading:loading,openModal:this.openModal.bind(this),openEdit:this.openEdit.bind(this)}),
      modal &&
        h(DeleteModal,{...modal,onClose:this.openModal.bind(this, null),btnClass:"danger",onComplete:this.load.bind(this)})
      ,
      edit &&
        h(EditModal,{onClose:this.openEdit.bind(this, null),title:" "},
          h(CategoryForm,{id:edit._id,submitFinish:this.load.bind(this),onClose:this.openEdit.bind(this, null),path:"/admin-data?categories",successMessage:"  !",confirmText:""}),
        )
      ,
    )
  }
}
