//       {this.state.data.map(({ _id, ...item }) => {
//         return <ItemRow key={_id} {...item} id={_id} onDelete={() => this.load()} />
//       })}
// const List =
const List = ({ data, openModal, openEdit, loading }) => {
  return (                                      h('div',{},
    loading && h('span',{'className':"echo-loader"},`Loading`),
    !loading && !data.length && '  .',
    data.map((item, i) => {
      const even = i % 2 == 0
      return h(Item,{item:item,key:item._id,openModal:openModal,openEdit:openEdit,style:`background:${!even ? '#edeee8' : 'white'};`})
    }),
  ))
}

class Item extends Component {
  render({ item, openModal, openEdit }) {
    const { title, image, description, seo, _id }   = item
    const s = `//${seo}/`
    const link = `knedv.ru${s}`

    return (h(Row,{className:"CategoryRow"},
      h(Col,{className:"col-3 col-sm-4 "},
        h('img',{'src':image,'className':"img-fluid p-1"}),
      ),
      h(Col,{},
        h('h2',{},title),
        h('em',{},h('a',{'href':s},link)),
        h('p',{},description),
      ),
      h(Col,{className:"col-1 CategoryMeta"},
        h(A,{onClick:() => {
          openEdit(item)
        },style:"color:brown;"},` `,h(Icon,{icon:"fas fa-pen"})),
        h('br'),
        h(A,{onClick:() => {
          openModal({
            modal: {
              text: h('span',{},`     `,h('strong',{},title),`?`),
              confirmText: '',
              title: ' ',
              path: `categories&id=${_id}&delete`,
            },
          })
        },style:"color:brown;"},h(Icon,{icon:"far fa-trash-alt"})),
      ),
    ))
  }
}

